* {
  box-sizing: border-box;
}

:root {
  --grey: #555;
}

ul {
  padding: 0;
  overflow-y: scroll;
}

.itemsStatus div {
  border: .5px solid gray;
  border-radius: 15px;
  justify-content: flex-start;
  margin: .5rem;
  display: flex;
}

.itemsStatus div img {
  width: 30px;
  margin: 10px;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  font-size: 10px;
}

body {
  text-align: center;
  background-color: #a8f0ec;
}

.list {
  width: 30rem;
  background-color: #f5f5f5;
  border-radius: 20px;
  margin: auto;
}

.header {
  height: 48px;
  background: linear-gradient(302deg, #020024 0%, #09796e 35%, #00d4ff 100%);
  border-radius: 20px 20px 0 0;
  padding: 8px;
  font-size: 24px;
}

.contents {
  height: 39rem;
  overflow-y: scroll;
}

.items .item .itemsStatus {
  margin: .5rem;
  padding: 5px 8px;
  display: grid;
}

.items .item .itemsStatus .divStatus {
  display: flex;
}

.item_delete {
  font-size: 16px;
  transition: all .3s ease-in-out;
}

.item_delete:hover {
  color: red;
  transform: scale(2);
}

.goToSecond {
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.footer {
  background: linear-gradient(302deg, #020024 0%, #09796e 35%, #00d4ff 100%);
  border-radius: 0 0 20px 20px;
}

.footer_input {
  width: 100%;
  height: 3rem;
  border: none;
  outline: none;
  padding: 10px 16px;
  font-size: 1.5rem;
}

.footer_button {
  width: 4rem;
  height: 4rem;
  color: #fff;
  background-color: #000;
  border-radius: 50%;
  transition: all .3s ease-in-out;
}

.footer_button:hover {
  transform: scale(1.2);
}

.container {
  background-color: #f5f5f5;
  grid-template-columns: repeat(5, 1fr);
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.container .item {
  width: 7rem;
  height: 10rem;
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  display: grid;
}

.container .item img {
  width: 4rem;
  height: auto;
}

.container .item p {
  color: #666;
  font-size: .8rem;
}

.myUser {
  text-align: center;
  border: 4px solid pink;
  border-radius: 10px;
}

.item img {
  margin: 0 auto;
}

.goToPersnalInfo {
  background-color: #b2b2f2;
}

